:root {
  --color-blue: #00acdc;
  --color-black: #000;
}

*, :before, :after {
  box-sizing: border-box;
}

body {
  font-optical-sizing: auto;
  font-variation-settings: "width" 100;
  color: #000;
  margin: 0;
  font-family: Roboto, Arial, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 16px;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding-inline: 50px;
  display: flex;
}

.nav {
  text-transform: uppercase;
  height: 60px;
  line-height: 60px;
}

.nav__item + .nav__item {
  margin-left: 20px;
  list-style: none;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  color: var(--color-black);
  justify-content: center;
  align-items: center;
  text-decoration: none;
  display: inline-flex;
}

.nav__link:hover {
  color: var(--color-blue);
}

.nav__link.is-active {
  color: var(--color-blue);
  position: relative;
}

.nav__link.is-active:after {
  content: "";
  background-color: var(--color-blue);
  border-radius: 4px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.logo {
  align-items: center;
  display: block;
}

.logo__img {
  width: 40px;
  height: 40px;
  display: flex;
}
